.profile-first {
  .overlay-wrapper {
    background: rgba(255, 255, 255, 0.784618);
    width: 100%;
    height: 0;
    z-index: 5;
    position: absolute;

    &.active {
      height: 100%;
    }
  }

  .overlay {
    background: rgba(255, 255, 255, 0.784618);
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 3;
  }

  &-div {
    position: relative;
  }

  &-title {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 10px;
    text-align: center;
    color: #000000;
    z-index: 4;
    position: absolute;
    top: -75px;
    right: 0;
    bottom: 0;
    left: 0;
  }

  &-subtitle {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #000000;
    margin-bottom: 42px;
    z-index: 4;
    position: absolute;
    top: -42px;
    right: 0;
    bottom: 0;
    left: 0;
  }

  .top-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: -1;

    .little-card{
      &.checked{
        z-index: 4;
      }
    }

    .swiper-container {
      position: static;
    }

    .swiper-button-next {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
    }

    .swiper-button-next::after {
      content: url('../../assets/images/icons/arrow.svg');
      transform: matrix(-1, 0, 0, 1, 0, 0);
      position: absolute;
      top: 50px;
      right: -45px;
      bottom: 0;
      left: 100%;
    }

    .swiper-button-prev {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
    }

    .swiper-button-prev::after {
      content: url('../../assets/images/icons/arrow.svg');
      position: absolute;
      top: 50px;
      right: 100%;
      bottom: 0;
      left: -45px;
    }

    .swiper-wrapper {
      padding: 15px 5px;

      .swiper-slide {
        display: block;
        width: 81px;
        height: 98px;
        margin-left: 10px;
        margin-right: 10px;
      }
    }
  }
}