.main-card{
  text-decoration: none;
  margin-bottom: 18px;
  box-sizing: border-box;

  .image{
    border-radius: 5px;
    margin-bottom: 16px;
    width: 262px;
    height: 190px;
    object-fit: cover;
  }

  .text{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    margin-bottom: 8px;
  }

  .time{
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    color: #4A4A4A;
    margin-bottom: 4px;
  }

  .price{
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
  }
}