.registration {
  .left {
    text-align: center;

    &-title {
      font-style: normal;
      font-weight: bold;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
      text-transform: uppercase;
      color: #000000;
      margin-bottom: 75px;

      &.second {
        margin-bottom: 23px;
      }
    }

    .first-form {
      margin-bottom: 56px;

      .name-inp {
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.482592);
        box-sizing: border-box;
        border-radius: 5px;
        padding: 20px 27px;
        font-style: normal;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        text-align: center;
        color: #000;
        display: block;
        margin-bottom: 26px;
        width: 100%;
      }

      .submit-btn {
        background: #41D481;
        border-radius: 5px;
        display: block;
        border: none;
        outline: none;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #FFFFFF;
        padding: 20px 138px;
        width: 100%;
        text-decoration: none;
      }
    }

    .second-form {
      margin-bottom: 56px;

      label {
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #787993;
        display: block;
        text-align: left;
        margin-bottom: 7px;
      }

      .inp-form {
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.482592);
        box-sizing: border-box;
        border-radius: 5px;
        padding: 20px 27px;
        font-style: normal;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        text-align: center;
        color: #000;
        display: block;
        margin-bottom: 26px;
        width: 100%;
      }

      .left-text {
        text-align: left;
        margin-bottom: 20px;
      }

      .back-link {
        background: #FFFFFF;
        box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.279127);
        border-radius: 5px;
        padding: 20px;
        display: block;

        img {
          transform: rotate(90deg);
        }
      }

      .submit-btn {
        background: #41D481;
        border-radius: 5px;
        display: block;
        border: none;
        outline: none;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #FFFFFF;
        padding: 20px;
        width: 100%;
        text-decoration: none;
      }
    }

    .third-form {
      margin-bottom: 56px;

      label {
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #787993;
        display: block;
        text-align: left;
        margin-bottom: 7px;
      }

      .inp-form {
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.482592);
        box-sizing: border-box;
        border-radius: 5px;
        padding: 20px 27px;
        font-style: normal;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        text-align: center;
        color: #000;
        display: block;
        margin-bottom: 26px;
        width: 100%;

        &.code {
          border: 1px solid #304FFF;
        }
      }

      .seconds-block {
        background: rgba(216, 216, 216, 0.46841);
        border-radius: 5px;
        font-style: normal;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        color: #414D6A;
        padding: 20px;
      }

      .top-text {
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: left;
        color: #787993;
        margin-bottom: 16px;
      }

      .submit-btn {
        background: #41D481;
        border-radius: 5px;
        display: block;
        border: none;
        outline: none;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #FFFFFF;
        padding: 20px;
        width: 100%;
        text-decoration: none;
      }
    }

    .fourth-form {
      margin-bottom: 56px;

      label {
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #787993;
        display: block;
        text-align: left;
        margin-bottom: 7px;
      }

      .inp-form {
        background: #FFFFFF;
        border: 1px solid rgba(151, 151, 151, 0.482592);
        box-sizing: border-box;
        border-radius: 5px;
        padding: 20px 27px;
        font-style: normal;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        text-align: center;
        color: #000;
        display: block;
        margin-bottom: 26px;
        width: 100%;
      }

      .top-text {
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: left;
        color: #787993;
        margin-bottom: 16px;
      }

      .submit-btn {
        background: #41D481;
        border-radius: 5px;
        display: block;
        border: none;
        outline: none;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #FFFFFF;
        padding: 20px;
        width: 100%;
        text-decoration: none;
      }
    }

    &-bottom {
      border-top: 1px solid #979797;
      padding: 33px 32px;
      position: relative;

      .yoki {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        color: #787993;
        background: #FFFFFF;
        padding: 7px 13px;
        display: inline-block;
        position: absolute;
        top: -17px;
        left: 170px;
        right: 185px;
      }

      .vxod-taklif {
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #4E72CA;
        display: block;
        text-decoration: none;
      }
    }
  }

  .right {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%), url('../../assets/images/register-right.jpg');
    padding: 185px 65px;
    height: 65vh;


    .open-for-you {
      width: 275px;
      height: 275px;
      background-color: #fff;
      border-radius: 50% 50% 50% 25px;
      padding: 79px 20px 40px 41px;

      &-img {
        margin-bottom: 7.62px;
      }

      &-text {
        font-style: normal;
        font-weight: bold;
        font-size: 19px;
        line-height: 21px;
        color: #787993;
        margin-bottom: 18.92px;
      }


    }
  }
}