.layout-wrapper{
  background: rgba(255, 255, 255, 0.784618);
  width: 100%;
  height: 0;
  z-index: 1;
  position: absolute;

  &.active{
    height: 100%;
  }
}


.block-menu{
  border-bottom: 1px solid #D5D5D5;
  margin-bottom: 7px;

  &-link{
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-decoration: none;
    color: #9B9B9B;
    padding: 10px 30px;
    display: inline-block;
    position: relative;

    &:first-child{
      padding-left: 0;
    }

    &::before{
      content: "";
      width: 128px;
      height: 2px;
      background-color: transparent;
      position: absolute;
      top: 97%;
      bottom: 0;
      right: 25%;
      left: 15%;
    }

    &.active{
      color: #4E72CA;

      &::before{
        background-color: #4E72CA;
      }
    }
  }

}