.footer-top{
  background: #282B30;
  padding: 23px 0;
  .container{
    display: flex;
    align-items: center;
    justify-content: space-between;

    .logo{
      display: flex;
      align-items: center;
      width: 237px;

      &-title{
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        mix-blend-mode: normal;
        opacity: 0.1;
        margin-left: 14px;
      }
    }

    .center{
      &-text{
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #98A4B7;
        width: 307px;
      }
    }
  }
}