.footer-bottom {
  background: #33383C;
  padding: 52px 0;

  .container {
    .row {
      border-bottom: 1px solid #4C565E;

      .title {
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        position: relative;

        &::before {
          content: url("../../assets/images/icons/keyboard-right-arrow-button-first.svg");
          width: 5.92px;
          height: 9.6px;
          position: absolute;
          top: 1px;
          bottom: 0;
          left: -12.08px;
        }

        &.second {
          &::before {
            content: url("../../assets/images/icons/keyboard-right-arrow-button-second.svg");
            width: 5.92px;
            height: 9.6px;
            position: absolute;
            top: 1px;
            bottom: 0;
            left: -12.08px;
          }
        }

        &.fourth {
          &::before {
            content: url("../../assets/images/icons/keyboard-right-arrow-button-fourth.svg");
            width: 5.92px;
            height: 9.6px;
            position: absolute;
            top: 1px;
            bottom: 0;
            left: -12.08px;
          }
        }

        &.fifth {
          &::before {
            content: url("../../assets/images/icons/keyboard-right-arrow-button-fifth.svg");
            width: 5.92px;
            height: 9.6px;
            position: absolute;
            top: 1px;
            bottom: 0;
            left: -12.08px;
          }
        }

        &.sixth {
          &::before {
            content: url("../../assets/images/icons/keyboard-right-arrow-button-sixth.svg");
            width: 5.92px;
            height: 9.6px;
            position: absolute;
            top: 1px;
            bottom: 0;
            left: -12.08px;
          }
        }

        &.seventh {
          &::before {
            content: url("../../assets/images/icons/keyboard-right-arrow-button-seventh.svg");
            width: 5.92px;
            height: 9.6px;
            position: absolute;
            top: 1px;
            bottom: 0;
            left: -12.08px;
          }
        }

        &.eighth {
          &::before {
            content: url("../../assets/images/icons/keyboard-right-arrow-button-eighth.svg");
            width: 5.92px;
            height: 9.6px;
            position: absolute;
            top: 1px;
            bottom: 0;
            left: -12.08px;
          }
        }
      }

      .footer {
        list-style-type: none;
        padding: 0;
        margin: 0;

        &-link {
          text-decoration: none;
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 29px;
          color: #878F99;
        }
      }
    }

    .bottom{
      display: flex;
      align-items: center;
      justify-content: space-between;

      &-link{
        font-size: 15px;
        line-height: 18px;
        text-decoration: none;
        display: block;
        padding: 27px 0;
        color: #878F99;

        &.active{
          color: #05F3B3;
        }
      }
    }
  }
}