.header {
  background: #FFFFFF;
  box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);
  padding: 17px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;

    .left {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 250px;
      text-decoration: none;

      .title {
        font-style: normal;
        font-weight: bold;
        font-size: 15px;
        line-height: 17px;
        color: #000000;
        margin-left: 14px;
      }
    }

    .menu {
      &__link {
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        text-decoration: none;
        padding: 35px;
      }
    }

    .right {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .language {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-right: 66px;

        .lang {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 17px;
          color: #4A4A4A;
          padding: 0 15px;
          text-decoration: none;

          &.marked {
            border-right: 3px solid #4A4A4A;
            position: relative;
            z-index: 2;

            &::before {
              content: "";
              width: 4px;
              height: 30px;
              display: block;
              position: absolute;
              top: -3px;
              right: 0;
              bottom: 0;
              left: 50%;
              background-color: rgba(246, 218, 194, 0.831324);
              z-index: -1;
              transform: rotate(45deg);
            }
          }
        }
      }

      .register {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .registration {
          font-style: normal;
          font-weight: bold;
          font-size: 17px;
          line-height: 21px;
          color: #4E72CA;
          padding: 0 15px;

          &:first-child {
            border-right: 3px solid #4E72CA;
            text-decoration: none;
          }
        }
      }
    }
  }
}

.top {
  margin-top: 32px;
  border-bottom: 1px solid rgba(151, 151, 151, 0.393739);

  .search-group {
    padding: 0 17px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid rgba(151, 151, 151, 0.496575);
    box-sizing: border-box;
    border-radius: 6px;
    margin-bottom: 12px;

    &-inp {
      border: none;
      background-color: transparent;
      display: block;
      width: 100%;
      outline: none;
      padding: 16px 17px;
    }
  }

  .hashtag {
    margin-bottom: 25px;

    .tag {
      text-decoration: none;
      background: #E5E3FF;
      border-radius: 3px;
      padding: 3px 10px;
      display: inline-block;
      color: #704FFF;
      margin: 0 6.5px;

      &-text {
        font-style: normal;
        font-weight: bold;
        font-size: 13px;
        line-height: 16px;
      }

      &.second {
        background: #E4F6FF;
        color: #57B4E2;
      }

      &.third {
        background: #D2FFDB;
        color: #47B95E;
      }

      &.fourth {
        background: #F6DAC2;
        color: #874C1A;
      }
    }

    .location {
      background: #D8D8D8;
      border-radius: 3px;
      display: inline-block;
      padding: 3px 25px 3px 13px;
      text-decoration: none;
      margin: 0 6.5px;

      &-text {
        font-style: normal;
        font-weight: bold;
        font-size: 13px;
        line-height: 16px;
        color: #787993;
        position: relative;

        &::after {
          content: url("../../assets/images/icons/pin.svg");
          position: absolute;
          top: 1px;
          right: -15px;
          bottom: 0;
        }
      }
    }
  }

  .rubrike {
    display: flex;
    align-items: center;
    margin-bottom: 23px;

    &-title {
      font-size: 16px;
      line-height: 19px;
      letter-spacing: -6.7332e-05px;
      color: #787993;
      margin-right: 54px;
    }

    &-block {
      background: #FFFFFF;
      border: 1px solid rgba(151, 151, 151, 0.496575);
      box-sizing: border-box;
      border-radius: 27px;
      padding: 7px 133px 7px 8px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;

      &::after {
        content: url('../../assets/images/icons/arrow-down.svg');
        position: absolute;
        top: 27%;
        right: 15px;
        bottom: 0;
      }

      .rubrika {
        background: #EBF5FF;
        border: 1px solid #B3D9FF;
        box-sizing: border-box;
        border-radius: 20.5px;
        padding: 11px 47px 11px 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        margin-right: 7px;

        &-text {
          font-style: normal;
          font-weight: 500;
          font-size: 15px;
          line-height: 18px;
          color: #2979FF;
        }

        &-btn {
          background: #EBF5FF;
          border: 1px solid #B3D9FF;
          box-sizing: border-box;
          border-radius: 20.5px;
          display: flex;
          align-items: center;
          justify-content: center;
          width: 41px;
          height: 41px;
          position: absolute;
          top: 0;
          bottom: 0;
          right: -2px;
        }
      }
    }
  }

  .price {
    display: flex;
    align-items: center;
    margin-bottom: 15px;

    &-title {
      font-family: Gilroy;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: -6.7332e-05px;
      color: #787993;
      margin-right: 41px;
    }
  }

  &-bottom {
    margin-bottom: 29px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-left {
      .select-inp {
        background: #FFFFFF;
        padding: 15px 19px;
        border: none;
        margin-right: 32px;
        outline: none;
      }
    }

    &-btn {
      background: #304FFF;
      box-shadow: 3px 6px 14px #9CC1FF;
      border-radius: 22px;
      padding: 14px 12px;
      font-style: normal;
      font-weight: bold;
      font-size: 15px;
      line-height: 19px;
      text-align: center;
      color: #FFFFFF;
      border: none;
      outline: none;
    }
  }
}

.main-section {
  margin-bottom: 26px;

  .row {
    padding-bottom: 54px;
    border-bottom: 1px solid rgba(182, 202, 251, 0.606827);
  }
}

.search-now {
  margin-bottom: 35px;

  &-title {
    display: flex;
    align-items: center;
    margin-bottom: 25px;

    &-img {
      margin-right: 7px;
    }

    &-text {
      font-style: normal;
      font-weight: bold;
      font-size: 20px;
      line-height: 25px;
      color: #414D6A;
    }
  }

  &-block {
    border-right: 1px solid rgba(151, 151, 151, 0.356686);
    border-left: 1px solid rgba(151, 151, 151, 0.356686);
    overflow-x: scroll;

    &::-webkit-scrollbar {
      display: none;
    }

    &-card {
      padding: 13px 26px;

      .title {
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 24px;
        color: #4E72CA;
      }

      .card-bottom {
        display: flex;
        align-items: center;

        &-img {
          width: 13px;
          height: 14px;
          object-fit: contain;
          margin-right: 6.75px;
        }

        &-title {
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 14px;
          color: #4A4A4A;
        }
      }
    }
  }
}

.most-interesting {
  margin-bottom: 40px;

  &-title {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 25px;
    color: #41D481;
    margin-bottom: 18px;
  }

  &-block {

    .swiper-button-next::after {
      content: url('../../assets/images/icons/arrow.svg');
      transform: matrix(-1, 0, 0, 1, 0, 0);
    }

    .swiper-button-prev::after {
      content: url('../../assets/images/icons/arrow.svg');
    }

    &-card {
      background: #FFFFFF;
      border: 1px solid rgba(120, 121, 147, 0.243461);
      box-sizing: border-box;
      padding: 23px 32px;
      display: inline-block;
      text-decoration: none;

      &-img {
        width: 300px;
        height: 218px;
        object-fit: cover;
      }

      &-title {
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 19px;
        color: #000000;
        margin-bottom: 8px;
      }

      &-time {
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 14px;
        margin-bottom: 4px;
        color: #4A4A4A;
      }

      &-price {
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
      }
    }

  }
}