.breadcrumb {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #787993;
  margin-bottom: 20px;
}

.title {
  font-style: normal;
  font-weight: 500;
  line-height: 41px;
  color: #414D6A;
  margin-bottom: 14px;
}

.mini-img {
  width: 118px;
  height: 85px;
  object-fit: cover;
  margin-bottom: 13px;

  &.active {
    width: 113px;
    height: 75px;
    object-fit: cover;
    border: 1px solid black;
    padding: 5px;
  }
}

.big-img {
  width: 456px;
  height: 456px;
  object-fit: cover;
}

.right {
  &-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 17px;

    &-span {
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: -5.89155e-05px;
      color: #414D6A;
    }
  }

  &-price {
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 49px;
    letter-spacing: -0.00016833px;
    color: #444444;
    margin-bottom: 8px;
  }

  &-location {
    display: block;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: -5.89154e-05px;
    text-decoration-line: underline;
    color: #4E72CA;
    margin-bottom: 14px;
  }

  &-number {
    &-link {
      background: #65E99F;
      border-radius: 18.5px;
      padding: 8px 23px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      margin-bottom: 7px;

      &:nth-child(2) {
        margin-bottom: 17px;
      }

      &-text {
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: -6.73319e-05px;
        color: #000000;
      }

      &-button {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        background: #41D481;
        border-radius: 18.5px;
        border: none;
      }
    }
  }

  .author {
    display: flex;
    align-items: center;
    margin-bottom: 24px;

    &-text {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: -3.17237e-05px;
      color: #000000;
      margin-right: 7px;
    }

    &-name {
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 17px;
      letter-spacing: -4.13787e-05px;
      text-decoration-line: underline;
      color: #2AAE63;
    }
  }

  &-btns {
    margin-bottom: 25px;

    &-btn {
      border-radius: 23.5px;
      font-size: 14px;
      line-height: 16px;
      letter-spacing: -5.89155e-05px;
      color: #FFFFFF;

      &.btn-purple {
        background: #4E72CA;
      }

      &.btn-primary {
        background: #304FFF;
        margin-right: 11px;
      }
    }
  }

  .about {
    margin-bottom: 10px;

    &-card {
      border: 1px solid rgba(151, 151, 151, 0.191604);
      padding: 19px 14px;

      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3) {
        border-top: none;
      }

      &:nth-child(3),
      &:nth-child(6),
      &:nth-child(9),
      &:nth-child(12),
      &:nth-child(13) {
        border-right: none;
      }

      &:nth-child(13) {
        border-bottom: none;
      }

      .title {
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -0.00016833px;
        color: #444444;
        margin-bottom: 5px;
      }

      .info {
        font-style: normal;
        font-weight: 500;
        font-size: 18px;
        line-height: 22px;
        letter-spacing: -0.00016833px;
        color: #444444;

        &.blue {
          text-decoration-line: underline;
          color: #4E72CA;
          cursor: pointer;
        }
      }
    }
  }

  &-bottom{
    margin-bottom: 97px;

    &-left{
      margin-right: 190px;
    }
  }
}

.bottom{
  margin-bottom: 55px;

  &-middle{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    .swiper-container {
      position: static;
      text-align: center;

      .main-card{
        text-align: left;
      
        .image{
          width: 100%;
        }
      }
      
    }

    .swiper-button-next {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
    }

    .swiper-button-next::after {
      content: url('../../assets/images/icons/arrow.svg');
      transform: matrix(-1, 0, 0, 1, 0, 0);
      position: absolute;
      top: 100px;
      right: -45px;
      bottom: 0;
      left: 100%;
    }

    .swiper-button-prev {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
    }

    .swiper-button-prev::after {
      content: url('../../assets/images/icons/arrow.svg');
      position: absolute;
      top: 100px;
      right: 100%;
      bottom: 0;
      left: -45px;
    }
  }
}