.second-menu{
  background: #FFF;
  box-shadow: 2px 4px 15px rgba(0, 0, 0, 0.209296);
  position: absolute;
  margin: 0 auto;
  display: block;
  width: 1143px;
  z-index: 2;
}

.main-menu {
  background: #FFF;
  box-shadow: 2px 4px 15px rgba(0, 0, 0, 0.209296);
  position: absolute;
  margin: 0 auto;
  display: none;
  width: 1143px;
  z-index: 2;

  &.active{
    display: block;
  }

  &-link {
    position: relative;
    list-style-type: none;

    a {
      text-decoration: none;
      font-style: normal;
      font-weight: 500;
      font-size: 16px;
      line-height: 39px;
      letter-spacing: -6.7332e-05px;
      color: #000000;

      &::before {
        content: "";
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #B4E2E1;
        border: 1px solid #4A90E2;
        position: absolute;
        top: 15px;
        left: -15px;
        bottom: 0;
      }
    }
  }
}