.add-card{
  border-radius: 10px;
  padding: 24px 151px 41px 30px;
  background: url("../../assets/images/notebook.png") ,linear-gradient(125deg, #B4E2E1 65%, #FF9E83 35%);
  background-repeat: no-repeat;
  background-position: right;
  text-decoration: none;
  display: block;
  width: 359px;
  height: 238px;
  box-sizing: border-box;
  margin-right: 29px;

  &.second{
  background: url("../../assets/images/gucci-collection.png"), linear-gradient(125deg, #F6DAC2 65%, #B6CAFB 35%);
  background-repeat: no-repeat;
  background-position: right top;
  }

  &.third {
    background: url("../../assets/images/office-collection.png"), linear-gradient(125deg, #F7E233 65%, #05F3B3 35%);
    background-repeat: no-repeat;
    background-position: right;
  }

  .text{
    font-size: 23px;
    line-height: 26px;
    color: #000000;
    margin-bottom: 77px;
  }

  .how-much{
    font-size: 15px;
    line-height: 18px;
    color: #4A4A4A;
    position: relative;

    &::before{
      content: url("../../assets/images/icons/vector.svg");
      width: 5.5px;
      height: 10px;
      position: absolute;
      top: 1px;
      right: -15px;
      bottom: 0;
    }
  }
}