.little {
  text-decoration: none;
  text-align: center;
  margin-left: 33px;
  margin-right: 33px;

  &-card {
    border-radius: 20px;
    padding: 14px 15px;
    margin-bottom: 6px;
    box-sizing: border-box;
    border: none;
    width: 75px;
    height: 75px;
    text-align: center;

    &.first {
      background: #B6CAFB;
    }

    &.second {
      background: #B4E2E1;
    }

    &.third {
      background: #FF9E83;
    }

    &.fourth {
      background: #F7E233;
    }

    &.fifth {
      background: #F6DAC2;
    }

    &.checked{
      z-index: 7;
    }
  }

  .title {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 17px;
    color: #4A4A4A;
  }
}