.header {
  background: #FFFFFF;
  box-shadow: 12px 4px 24px rgba(0, 0, 0, 0.0647645);
  padding: 17px;

  &-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;

    .left {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 250px;
      text-decoration: none;

      .title {
        font-style: normal;
        font-weight: bold;
        font-size: 15px;
        line-height: 17px;
        color: #000000;
        margin-left: 14px;
      }
    }

    .menu {
      &__link {
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        text-decoration: none;
        padding: 35px;
      }
    }

    .right {
      display: flex;
      align-items: center;
      justify-content: space-between;

      .language {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-right: 66px;

        .lang {
          font-style: normal;
          font-weight: 500;
          font-size: 14px;
          line-height: 17px;
          color: #4A4A4A;
          padding: 0 15px;
          text-decoration: none;

          &.marked {
            border-right: 3px solid #4A4A4A;
            position: relative;
            z-index: 2;

            &::before {
              content: "";
              width: 4px;
              height: 30px;
              display: block;
              position: absolute;
              top: -3px;
              right: 0;
              bottom: 0;
              left: 50%;
              background-color: rgba(246, 218, 194, 0.831324);
              z-index: -1;
              transform: rotate(45deg);
            }
          }
        }
      }

      .register {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .registration {
          font-style: normal;
          font-weight: bold;
          font-size: 17px;
          line-height: 21px;
          color: #4E72CA;
          padding: 0 15px;


          &:first-child {
            border-right: 3px solid #4E72CA;
            text-decoration: none;
          }
        }
      }
    }
  }

  .bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .left {
      form {
        width: 872px;
        display: flex;
        align-items: center;
        position: relative;


        .header-select {
          font-style: normal;
          font-weight: 500;
          font-size: 15px;
          line-height: 18px;
          color: #000000;
          padding: 14px 25px;
          border: 1px solid rgba(151, 151, 151, 0.496575);
          box-sizing: border-box;
          background-position-x: 180px;
          -webkit-appearance: none;


          &:first-child {
            border-radius: 22.5px 0 0 22.5px;
          }

          &:nth-child(2) {
            border-radius: 0;
            padding-right: 35px;
          }

          &:nth-child(3) {
            border-radius: 0 22.5px 22.5px 0;
            padding-right: 55px;
          }
        }

        button[type=submit] {
          background: #19D476;
          border-radius: 22px;
          font-style: normal;
          font-weight: bold;
          font-size: 14px;
          line-height: 17px;
          color: #FFFFFF;
          padding: 15px 20px;
          border: none;
          margin-left: -5px;
          position: absolute;
          right: 0;
          top: 0;
          bottom: 0;
        }

      }
    }

    &-right {
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: #19D476;
      border-radius: 22px;
      padding: 13px 30px 13px 56px;
      text-decoration: none;
      font-style: normal;
      font-weight: bold;
      font-size: 15px;
      line-height: 19px;
      color: #FFFFFF;
      position: relative;

      &::before {
        content: "+";
        background: #11B463;
        width: 44px;
        height: 44px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        border-radius: 50%;
        font-size: 28px;
        line-height: 33px;
        text-align: center;
        padding: 5px 14px;
      }
    }

  }
}