.top-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;

  .swiper-container{
    position: static;
  }
  
  .swiper-button-next{
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
  }
  .swiper-button-next::after {
    content: url('../../assets/images/icons/arrow.svg');
    transform: matrix(-1, 0, 0, 1, 0, 0);
    position: absolute;
    top: 50px;
    right: -45px;
    bottom: 0;
    left: 100%;
  }

  .swiper-button-prev {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
  }

  .swiper-button-prev::after {
    content: url('../../assets/images/icons/arrow.svg');
    position: absolute;
    top: 50px;
    right: 100%;
    bottom: 0;
    left: -45px;
  }

  .swiper-wrapper {
    padding: 15px 5px;
    z-index: 2;

    .swiper-slide {
      display: block;
      width: 81px;
      height: 98px;
      margin-left: 10px;
      margin-right: 10px;
    }
  }

}

.main-block {
  &-middle {
    border-bottom: 1px solid rgba(182, 202, 251, 0.606827);
  }

  &-bottom {
    text-align: center;

    &-link {
      font-size: 15px;
      line-height: 18px;
      color: #4E72CA;
      position: relative;
      text-decoration: none;
      padding: 18px 0;
      display: inline-block;
      margin: 0 auto;

      &::before {
        content: url("../../assets/images/icons/vector.svg");
        position: absolute;
        top: 3px;
        right: -8px;
        bottom: 0;
        transform: rotate(90deg);
      }

      &.second{
        &::before{
          content: url("../../assets/images/icons/grey-vector.svg");
          position: absolute;
          top: 18px;
          right: -15px;
          bottom: 0;
          transform: rotate(0deg);
        }
      }
    }
  }
}

.add-block {
  background: rgba(182, 202, 251, 0.159986);
  box-shadow: inset 0px 21px 13px rgba(182, 202, 251, 0.225402);
  padding: 50px 0;
}

.bottom-block{
  .container{
    .bottom-block-middle{
      border-bottom: 1px solid rgba(182, 202, 251, 0.606827);
    }
  }
}